import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-countdown',
  templateUrl: './countdown.component.html',
  styleUrls: ['./countdown.component.scss']
})
export class CountdownComponent implements OnInit {

  @Input() date: string;

  public timerdate;
  public now;
  private intervalId: any;

  constructor() {
    // Actualiza `now` cada segundo
    this.intervalId = window.setInterval(() => {
      this.now = Math.trunc(new Date().getTime() / 1000);
      if (this.now >= this.timerdate) {
        this.increaseDay(); // Aumenta el da cuando el contador llegue a cero
      }
    }, 1000);
  }

  ngOnInit(): void {
    this.timerdate = Math.trunc(new Date(this.date).getTime() / 1000);
    this.now = Math.trunc(new Date().getTime() / 1000);
  }

  // Aumenta el da cuando el contador llega a cero
  increaseDay(): void {
    const nextDay = new Date(this.date);
    nextDay.setDate(nextDay.getDate() + 1); // Aumentar un da
    this.date = nextDay.toISOString().split('T')[0] + ' ' + nextDay.toTimeString().split(' ')[0]; // Actualiza la fecha
    this.timerdate = Math.trunc(new Date(this.date).getTime() / 1000); // Reinicia el contador con la nueva fecha
  }

  get seconds() {
    return (this.timerdate - this.now) % 60
  }

  get minutes() {
    return Math.trunc((this.timerdate - this.now) / 60) % 60
  }

  get hours() {
    return Math.trunc((this.timerdate - this.now) / 60 / 60) % 24
  }

  get days() {
    return Math.trunc((this.timerdate - this.now) / 60 / 60 / 24)
  }

}
