import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProductLeftSidebarComponent } from './product/sidebar/product-left-sidebar/product-left-sidebar.component';
import { ProductRightSidebarComponent } from './product/sidebar/product-right-sidebar/product-right-sidebar.component';
import { ProductNoSidebarComponent } from './product/sidebar/product-no-sidebar/product-no-sidebar.component';
import { ThreeColumnComponent } from './product/three-column/three-column.component';
import { FourImageComponent } from './product/four-image/four-image.component';
import { BundleProductComponent } from './product/bundle-product/bundle-product.component';
import { ImageOutsideComponent } from './product/image-outside/image-outside.component';

import { ArtesaniasLeftSidebarComponent } from './seccion/artesanias-left-sidebar/artesanias-left-sidebar.component';
import { JoyeriaLeftSidebarComponent } from './seccion/joyeria-left-sidebar/joyeria-left-sidebar.component';
import { RopaLeftSidebarComponent } from './seccion/ropa-left-sidebar/ropa-left-sidebar.component';

import { CartComponent } from './cart/cart.component';
import { WishlistComponent } from './wishlist/wishlist.component';
import { CompareComponent } from './compare/compare.component';
import { CheckoutComponent } from './checkout/checkout.component';
import { SuccessComponent } from './checkout/success/success.component';

import { Resolver } from '../shared/services/resolver.service';
import { BolsosLeftSidebarComponent } from './seccion/bolsos-left-sidebar/bolsos-left-sidebar.component';
import { RegalosLeftSidebarComponent } from './categoria/regalos-left-slider/regalos-left-sidebar.component';
import { DecorativasLeftSidebarComponent } from './categoria/decorativas-left-slider/decorativas-left-sidebar.component';
import { HogarLeftSidebarComponent } from './categoria/hogar-left-slider/hogar-left-sidebar.component';
import { OficinaLeftSidebarComponent } from './categoria/oficina-left-slider/oficina-left-sidebar.component';
import { JoyeriaCategoriaLeftSidebarComponent } from './categoria/joyeria-categoria-left-slider/joyeria-categoria-left-sidebar.component';
import { AccesorioRopaLeftSidebarComponent } from './categoria/accesorio-ropa-left-slider/accesorio-ropa-left-sidebar.component';
import { RopaHombreLeftSidebarComponent } from './categoria/ropa-hombre-left-slider/ropa-hombre-left-sidebar.component';
import { RopaMujerLeftSidebarComponent } from './categoria/ropa-mujer-left-slider/ropa-mujer-left-sidebar.component';
import { MochilasLeftSidebarComponent } from './categoria/mochilas-left-slider/mochilas-left-sidebar.component';
import { BilleteraLeftSidebarComponent } from './categoria/billetera-left-slider/billetera-left-sidebar.component';


const routes: Routes = [
  {
    path: 'product/left/sidebar/:slug',
    component: ProductLeftSidebarComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/right/sidebar/:slug',
    component: ProductRightSidebarComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/no/sidebar/:slug',
    component: ProductNoSidebarComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/three/column/:slug',
    component: ThreeColumnComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/four/image/:slug',
    component: FourImageComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/bundle/:slug',
    component: BundleProductComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'product/image/outside/:slug',
    component: ImageOutsideComponent,
    resolve: {
      data: Resolver
    }
  },
  {
    path: 'seccion/artesanias',
    component: ArtesaniasLeftSidebarComponent
  },
  {
    path: 'seccion/joyeria',
    component: JoyeriaLeftSidebarComponent
  },
  {
    path: 'seccion/ropa',
    component: RopaLeftSidebarComponent
  },
  {
    path: 'seccion/bolsos',
    component: BolsosLeftSidebarComponent
  },
  //Rutas componentes
  {
    path: 'categoria/regalos',
    component: RegalosLeftSidebarComponent
  },
  {
    path: 'categoria/decorativas',
    component: DecorativasLeftSidebarComponent
  },
  {
    path: 'categoria/hogar',
    component: HogarLeftSidebarComponent
  },
  {
    path: 'categoria/oficina',
    component: OficinaLeftSidebarComponent
  },
  {
    path: 'categoria/joyeria',
    component: JoyeriaCategoriaLeftSidebarComponent
  },
  {
    path: 'categoria/accesorios-ropa',
    component: AccesorioRopaLeftSidebarComponent
  },
  {
    path: 'categoria/ropa-hombre',
    component: RopaHombreLeftSidebarComponent
  },
  {
    path: 'categoria/ropa-mujer',
    component: RopaMujerLeftSidebarComponent
  },
  {
    path: 'categoria/mochilas',
    component: MochilasLeftSidebarComponent
  },
  {
    path: 'categoria/billetera',
    component: BilleteraLeftSidebarComponent
  },
  {
    path: 'cart',
    component: CartComponent
  },
  {
    path: 'wishlist',
    component: WishlistComponent
  },
  {
    path: 'compare',
    component: CompareComponent
  },
  {
    path: 'checkout',
    component: CheckoutComponent
  },
  {
    path: 'checkout/success',
    component: SuccessComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ShopRoutingModule { }
